<template>
  <v-data-table v-bind="$attrs">
  <template v-slot:item.created_on="{ item }" >
       {{ item.created_on.split('T')[0] }} 
    </template>
    <template v-slot:item.payment_method="{ item }"  >
      {{ item.payment_method.name  }}  ({{ item.payment_method.type }})
    </template>
    <template v-slot:item.amount="{ item }"  >
      {{  new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.amount) }}
    </template>
    <template v-slot:item.created_on="{ item }"  >
      {{ new Intl.DateTimeFormat().format(new Date(item.created_on)) }}
    </template>
  <template v-slot:footer="data">
    
    <v-btn icon absolute :disabled="!data.props.pagination.itemsLength" small @click="$emit('expand-table')"><v-icon>mdi-window-maximize</v-icon></v-btn>
  </template>
  </v-data-table>
</template>
<script>
 

export default {
  name: 'data-table',
}
</script>