<template>
  <v-content class="pa-0 ma-0">
  <v-toolbar flat  color="green white--text" class="title"  >
   Total Day : {{ new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(todayAll) }}
   <v-spacer />
   <!-- <v-icon class="white--text">mdi-calendar</v-icon> -->
  
   <app-date-picker v-model="date" @input="updateTotal" prepend-inner-icon="mdi-calendar" reverse  outlined hide-details dense dark class="mt-5" style="width:200px" />
   <!-- {{ new Intl.DateTimeFormat().format(new Date( )) }} -->

  </v-toolbar>
    <v-row class="pa-0 ma-0">
      <v-col><h2> sale</h2></v-col>
      <v-col><h2> Retention</h2></v-col>
      <v-col cols="3"><h2> Total</h2></v-col>
    </v-row>
    <v-divider></v-divider>
    <h3 class="pt-1 grey--text">English</h3>
    <v-row>
    
      <v-col>
       
       <app-data-table  dense
          height="164"
          :headers="headers.sale"
          :items="items.sale.en"
           @expand-table="expandTable('sale', 'en')"
           
          multi-sort
          class="elevation-1"
        :footer-props="fprops"
        >
      
        </app-data-table>
       
      </v-col>
       
       <v-col>
        

      <app-data-table dense
          :footer-props="fprops"
          :headers="headers.retention"
          :items="items.retention.en"
          @expand-table="expandTable('retention', 'en')"
          height="164"
          multi-sort
          class="elevation-1"
        >
        </app-data-table>
      </v-col>
      <v-col >
        <v-data-table   
            hide-default-footer
          :headers="headers.total"
          :items="items.total.en"
          height="200"
          multi-sort
          class="elevation-1"
        >
        <template v-slot:body.append >
          <tr>
          <td>Total</td>
          <td></td>
          <td class="green--text font-weight-bold">{{ new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(totals.en)}}</td>
          </tr>
        </template>
        </v-data-table>
       
      </v-col>
    </v-row>
    <v-row>
        <total-sheet title="(EN) Total Month" :items="[
          { 
            title: 'Sales',
            ftd: totalMonth.sales.en.ftd,
            total: totalMonth.sales.en.total
          },
          { 
            title: 'Retentions',
            ftd: totalMonth.retention.en.ftd,
            total: totalMonth.retention.en.total
          },
          { 
            title: 'Total',
            ftd: totalMonth.sales.en.ftd+totalMonth.retention.en.ftd,
            total: totalMonth.sales.en.total+totalMonth.retention.en.total
          }
        ]"></total-sheet>
       
    </v-row>
    <v-divider></v-divider>
    <h3 class="pt-1 grey--text">Arabic</h3>

    <v-row>
      <v-col>
       <app-data-table  dense
          :footer-props="fprops"
          :headers="headers.sale"
          :items="items.sale.ar"
          @expand-table="expandTable('sale', 'ar')"
          height="164"
          multi-sort
          class="elevation-1"
        >
        </app-data-table>
        
      </v-col>
       <v-col>

      <app-data-table dense  
      :footer-props="fprops"
          :headers="headers.retention"
          :items="items.retention.ar"
          @expand-table="expandTable('retention', 'ar')"
          height="164"
          multi-sort
          class="elevation-1"
        >
        </app-data-table>
      </v-col>
      <v-col >
     
        <v-data-table    
            hide-default-footer
          :headers="headers.total"
          :items="items.total.ar"
          height="200"
          multi-sort
          class="elevation-1"
        >
        <template v-slot:body.append >
          <tr>
          <td>Total</td>
          <td></td>
          <td class="green--text font-weight-bold">{{ new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(totals.ar)}}</td>
          </tr>
        </template>
        </v-data-table>
        
      </v-col>
       
    </v-row>

    <v-row>
        <total-sheet title="(AR) Total Month" :items="[
          { 
            title: 'Sales',
            ftd: totalMonth.sales.ar.ftd,
            total: totalMonth.sales.ar.total
          },
          { 
            title: 'Retentions',
            ftd: totalMonth.retention.ar.ftd,
            total: totalMonth.retention.ar.total
          },
          { 
            title: 'Total',
            ftd: totalMonth.sales.ar.ftd+totalMonth.retention.ar.ftd,
            total: totalMonth.sales.ar.total+totalMonth.retention.ar.total
          }
        ]"></total-sheet>
      
    </v-row>
    <v-divider></v-divider>
    <h3 class="pt-1 grey--text">Label 1</h3>

    <v-row>
      <v-col>
       <app-data-table  dense
          :footer-props="fprops"
          :headers="headers.sale"
          :items="items.sale.label1"
          @expand-table="expandTable('sale', 'label1')"
          height="164"
          multi-sort
          class="elevation-1"
        >
        </app-data-table>
        
      </v-col>
       <v-col >

      <app-data-table dense
      :footer-props="fprops"
          :headers="headers.retention"
          :items="items.retention.label1"
          @expand-table="expandTable('retention', 'label1')"
          height="164"
          multi-sort
          class="elevation-1"
        >
        </app-data-table>
      </v-col>
      <v-col >
      <v-data-table   
            hide-default-footer
          :headers="headers.total"
          :items="items.total.label1"
          height="200"
          multi-sort
          class="elevation-1"
        >
        <template v-slot:body.append >
          <tr>
          <td>total</td>
          <td></td>
          <td class="green--text font-weight-bold">{{ new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(totals.label1)}}</td>
          </tr>
        </template>
        </v-data-table>
   
      </v-col>
       
    </v-row>
     <v-row>
 
      <total-sheet title="(Label1) Total Month" :items="[
          { 
            title: 'Sales',
            ftd: totalMonth.sales.lb.ftd,
            total: totalMonth.sales.lb.total
          },
          { 
            title: 'Retentions',
            ftd: totalMonth.retention.lb.ftd,
            total: totalMonth.retention.lb.total
          },
          { 
            title: 'Total',
            ftd: totalMonth.sales.lb.ftd+totalMonth.retention.lb.ftd,
            total: totalMonth.sales.lb.total+totalMonth.retention.lb.total
          }
        ]"></total-sheet>
    </v-row>

   <v-dialog v-model="dialog" max-width="950px"  >
  
      <v-card>
        <v-card-title>
        
          <v-spacer></v-spacer>
          <v-btn icon @click="dialog = false">
            <v-icon> mdi-close </v-icon>
          </v-btn>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
          
         <v-data-table dense v-if="dialog"
          :headers="headers.tables[dialogProps.collection]"
          :items="items[dialogProps.collection][dialogProps.bu]"
          multi-sort
          class="elevation-1"
          
        >
        <template v-slot:item.created_on="{ item }"  >
          {{ new Intl.DateTimeFormat().format(new Date(item.created_on ))   }} 
        </template>
        <template v-slot:item.payment_method="{ item }" >
          {{ item.payment_method.name  }}  ({{ item.payment_method.type }})
        </template>
        </v-data-table>
            </v-row>
          </v-container>
      
        </v-card-text>
      
      </v-card>
   
   </v-dialog>

  </v-content>
</template>
<script  >
import { mapActions } from 'vuex'
// import LineChart from './../components/ChartLine' 
import AppDataTable from  './../components/DataTable' 
import AppDatePicker from './../components/DatePicker'
import TotalSheet from './../components/TotalSheet'
 
import collections from './../collections'
export default {
  components: { 
     AppDatePicker,
     AppDataTable,
     TotalSheet },
  
  data () {
    return {
      totalMonth: {
         sales: {
           en: { ftd: 0, total: 0 },
           ar: { ftd: 0, total: 0 },
           lb: { ftd: 0, total: 0 }
         },
         retention: {
           en: { ftd: 0, total: 0 },
           ar: { ftd: 0, total: 0 },
           lb: { ftd: 0, total: 0 }
         }
      },

      date: new Date().toISOString().substr(0, 10),
      dialog: false,
      dialogProps: {
        collection: 'sale',
        bu: 'en'
      },
      loaded: false,
      chartdata: null,
      fprops: {
        itemsPerPageOptions: [5], // -> Add this example
      
        firstIcon: 'mdi-arrow-collapse-left',
        lastIcon: 'mdi-arrow-collapse-right',
        prevIcon: 'mdi-arrow-left',
        nextIcon: 'mdi-arrow-right'
      },
      todayAll:0,
      totals: {
          en: 0,
          ar: 0,
          label1: 0
      },
      headers: {
        tables: {
          sale: collections.get('sale').table,
          retention: collections.get('retention').table

        },
        total: [
           {
              text: 'Type',
              align: 'start',
              sortable: false,
              value: 'type',
            },
            // { text: 'Account name', value: 'name' },
            { text: 'FT"D', value: 'count' },
            { text: 'Total', value: 'amount' }
        ],
        sale: [
          {
              text: 'TP Account',
              align: 'start',
              sortable: false,
              value: 'tpc',
            },
            // { text: 'Account name', value: 'name' },
            { text: 'Amount', value: 'amount' },
            { text: 'Affiliate', value: 'affiliate' },
        ],
        retention: [{
              text: 'TP Account',
              align: 'start',
              sortable: false,
              value: 'tpc',
            },
            // { text: 'Account name', value: 'name' },
            { text: 'Amount', value: 'amount' },
            { text: 'Affiliate', value: 'affiliate' }]

      },
      items: {
        sale: {
          en: [],
          ar: [],
          label1: []
        },
        retention: {
          en: [],
          ar: [],
          label1: []
        },
        total: {
          en: [],
          ar: [],
          label1: []
        }

      },
      options:{ }
    }
  },
  computed: {
    firstDate () {
      const date = new Date()
      return new Intl.DateTimeFormat().format(new Date(date.getFullYear(), date.getMonth(), 1));
    },
    currentDate () {
      return new Intl.DateTimeFormat().format(new Date());
    }
  },
  methods: {
    ...mapActions({
      get: 'api/get'
    }),
    async updateTotal () {
      this.items.total = {
          en: [],
          ar: [],
          label1: []
        }
    const totals = await this.get({ collection: 'dashboard/total', query: {date: this.date} })
    const monthTotal = await this.get({ collection: 'dashboard/month' }) 
    if (monthTotal.status == 200) {
    
      monthTotal.data.sales.forEach(e => {
        this.totalMonth.sales[e._id].ftd = e.count 
        this.totalMonth.sales[e._id].total = e.amount 
      })
      monthTotal.data.rts.forEach(e => {
        this.totalMonth.retention[e._id].ftd = e.count 
        this.totalMonth.retention[e._id].total = e.amount 
      })
    
   
    }
    if (totals.status == 200) {
      this.todayAll = totals.data.total
      this.totals.en = totals.data.en
      this.totals.ar = totals.data.ar
      this.totals.label1 = totals.data.label1

    }
     
    this.loaded = false
    try {
      [
        {
          collection: 'sale',
          business_unit: 'en'
        },
        {
          collection: 'retention',
          business_unit: 'en'
        },
        {
          collection: 'sale',
          business_unit: 'ar'
        },
        {
          collection: 'retention',
          business_unit: 'ar'
        },
        {
          collection: 'sale',
          business_unit: 'label1'
        },
        {
          collection: 'retention',
          business_unit: 'label1'
        } 
      ].forEach(async e => {
        const  { data } = await this.get({ collection: 'dashboard/'+e.collection, query: {
          business_unit: e.business_unit,
          date: this.date
        } }) 
        this.items[e.collection][e.business_unit] = data
        
        this.items.total[e.business_unit].push({
            type: e.collection,
            count: data.length,
            amount: new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(data.length ? data.map((a) => a.amount ).reduce((a,b) => a + b) : 0)
        })
      })
        
      
    
      this.loaded = true
    } catch (e) {
      console.error(e)
    }
    },
    expandTable (type, bu) {
      this.dialogProps.collection = type
      this.dialogProps.bu = bu
      this.dialog = true
      console.log(type, bu)
    } 
  },
  
  async mounted () {
    this.updateTotal()

    


  }
}
</script>

 